import React, { useState, useEffect } from 'react';
import { 
  Box, 
  Typography, 
  List, 
  ListItem, 
  Divider, 
  CircularProgress, 
  Alert,
  TextField,
  IconButton,
  Checkbox,
  Menu,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  FormControlLabel,
  RadioGroup,
  Radio,
  FormControl,
  InputLabel,
  Select,
  InputAdornment
} from '@mui/material';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import { format, parseISO, isValid, isToday, isBefore, startOfDay } from 'date-fns';
import { ja } from 'date-fns/locale';
import { useTodo } from '../contexts/TodoContext';
import SettingsDialog from './SettingsDialog';

// Sidebar
const filters = [
  { id: 'today', name: '', icon: '' },
  { id: 'after-tomorrow', name: '', icon: '' },
  { id: 'past', name: '', icon: '' },
  { id: 'starred', name: '', icon: '' },
  { id: 'all', name: '', icon: '' },
];

// 
const categoryColors = {
  'default': '#1976d2'
};

const TodoList = ({ isMobile }) => {
  const { 
    todos, 
    taskLists, 
    selectedTaskList, 
    selectedFilter,
    showCompleted,
    loading, 
    error,
    toggleShowCompleted,
    createTask,
    moveTaskToList,
    deleteTask,
    toggleTaskCompletion,
    updateTask,
    reorderTasks,
    deleteTaskList
  } = useTodo();
  
  const [menuAnchorEl, setMenuAnchorEl] = useState(null);
  const [selectedTask, setSelectedTask] = useState(null);
  const [newTaskTitle, setNewTaskTitle] = useState('');
  
  // 
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [deleteTaskDialogOpen, setDeleteTaskDialogOpen] = useState(false);
  const [taskToDelete, setTaskToDelete] = useState(null);
  
  // 
  const [openDialog, setOpenDialog] = useState(false);
  const [editMode, setEditMode] = useState(false); // 
  const [taskDetails, setTaskDetails] = useState({
    title: '',
    description: '',
    dueDate: '',
    priority: 'normal',
    categoryId: '',
    taskId: null // ID
  });
  
  // 
  const [settingsDialogOpen, setSettingsDialogOpen] = useState(false);

  // 
  const selectedListInfo = taskLists?.find(list => list.id === selectedTaskList) || { title: '' };

  // 
  const selectedFilterInfo = filters.find(filter => filter.id === selectedFilter);

  // 
  const getListTitle = () => {
    if (selectedFilter && selectedFilter !== 'all') {
      // 
      return selectedFilterInfo?.name || '';
    } else if (selectedFilter === 'all') {
      // 
      return '';
    } else if (!selectedTaskList) {
      // 
      return '';
    } else {
      // 
      return selectedListInfo.title;
    }
  };

  // 
  const getTaskListName = (task) => {
    if (!task || !task.listId) return '';
    const list = taskLists.find(list => list.id === task.listId);
    return list ? list.title : '';
  };

  // ID
  const getListId = (task) => {
    return `: ${task.listId.substring(0, 8)}...`;
  };

  // 
  const getThemeColor = (type) => {
    // CSS
    switch (type) {
      case 'primary':
        return 'var(--primary-color)';
      case 'secondary':
        return 'var(--secondary-color)';
      case 'accent':
        return 'var(--accent-color)';
      case 'background':
        return 'var(--background-color)';
      case 'text':
        return 'var(--text-color)';
      case 'border':
        return 'var(--border-color)';
      default:
        return 'var(--primary-color)';
    }
  };
  
  // 
  const getTaskBackgroundColor = (task) => {
    if (!task.startDate) return 'var(--task-normal-color)'; // 
    
    try {
      const date = parseISO(task.startDate);
      const today = new Date();
      
      // : 
      if (isBefore(date, startOfDay(today))) {
        return 'var(--task-overdue-color)';
      }
      // : 
      else if (isToday(date)) {
        return 'var(--task-today-color)';
      }
      // : 
      else {
        return 'var(--task-normal-color)';
      }
    } catch (e) {
      return 'var(--task-normal-color)';
    }
  };

  // 
  const handleOpenDeleteDialog = () => {
    setDeleteDialogOpen(true);
    setMenuAnchorEl(null); // 
  };
  
  // 
  const handleOpenSettingsDialog = () => {
    setSettingsDialogOpen(true);
    setMenuAnchorEl(null); // 
  };

  // 
  const handleCloseDeleteDialog = () => {
    setDeleteDialogOpen(false);
  };

  // 
  const handleDeleteTaskList = async () => {
    try {
      await deleteTaskList(selectedTaskList);
      handleCloseDeleteDialog();
    } catch (err) {
      console.error('Failed to delete task list:', err);
    }
  };

  // 
  const handleMenuOpen = (event, task) => {
    setMenuAnchorEl(event.currentTarget);
    setSelectedTask(task);
  };

  // 
  const handleMenuClose = () => {
    setMenuAnchorEl(null);
    setSelectedTask(null);
  };

  // 
  const handleCategoryChange = (listId) => {
    if (selectedTask && listId) {
      // 
      moveTaskToList(selectedTask.id, listId);
    }
    handleMenuClose();
  };

  // 
  const moveTask = (fromIndex, toIndex) => {
    if (fromIndex === toIndex) return;
    
    const updatedTasks = [...todos];
    const [movedTask] = updatedTasks.splice(fromIndex, 1);
    updatedTasks.splice(toIndex, 0, movedTask);
    
    // 
    // 
    reorderTasks(updatedTasks);
  };

  // 
  const [sortOrder, setSortOrder] = useState('custom'); // 'custom'  'dueDate'
  
  // react-beautiful-dnd
  const handleDragEndRbd = (result) => {
    // 
    if (!result.destination) return;
    if (result.destination.index === result.source.index) return;

    // 
    moveTask(result.source.index, result.destination.index);
  };
  
  // 
  const [searchQuery, setSearchQuery] = useState('');

  // 
  const handleSearchChange = (e) => {
    setSearchQuery(e.target.value);
  };

  // 
  const handleEditTask = (task) => {
    // YYYY-MM-DD
    let formattedDate = '';
    if (task.startDate) {
      try {
        const date = parseISO(task.startDate);
        if (isValid(date)) {
          formattedDate = format(date, 'yyyy-MM-dd');
        }
      } catch (e) {
        console.error(':', e);
      }
    }

    // 
    setTaskDetails({
      taskId: task.id,
      title: task.title,
      description: task.notes || '', // notes
      dueDate: formattedDate,
      priority: task.starred ? 'starred' : 'normal',
      categoryId: task.listId || ''
    });
    
    // 
    setEditMode(true);
    setOpenDialog(true);
  };

  // 
  const handleSaveTask = async () => {
    if (!taskDetails.title.trim()) return;

    try {
      // 
      const isStarred = taskDetails.priority === 'starred';
      console.log(`Saving task with priority: ${taskDetails.priority}, starred: ${isStarred}`);
      
      // 
      let dueDate = null;
      if (taskDetails.dueDate) {
        try {
          // Date
          const dateObj = new Date(taskDetails.dueDate);
          // 23:59:59
          dateObj.setHours(23, 59, 59, 999);
          // ISO
          dueDate = dateObj.toISOString();
          console.log(`Setting due date to: ${dueDate} from input: ${taskDetails.dueDate}`);
        } catch (dateError) {
          console.error('Error formatting date:', dateError);
          // 
          dueDate = taskDetails.dueDate;
        }
      } else {
        console.log('No due date provided, setting to null');
      }
      
      const taskData = {
        title: taskDetails.title.trim(),
        notes: taskDetails.description || '',
        // Google Tasks API
        due: dueDate,
        // 
        starred: isStarred
      };

      console.log('Task data to be saved:', JSON.stringify(taskData, null, 2));

      if (editMode) {
        // 
        console.log(`Updating task ${taskDetails.taskId} with data:`, JSON.stringify(taskData, null, 2));
        
        // 
        const currentTask = todos.find(task => task.id === taskDetails.taskId);
        console.log('Current task before update:', currentTask);
        
        // 
        if (currentTask && taskDetails.categoryId && currentTask.listId !== taskDetails.categoryId) {
          // moveTaskToList
          console.log(`Moving task from list ${currentTask.listId} to ${taskDetails.categoryId}`);
          await moveTaskToList(taskDetails.taskId, taskDetails.categoryId);
        }
        
        // 
        await updateTask(taskDetails.taskId, taskData);
      } else {
        // 
        // taskDetails.categoryIdselectedTaskList
        const listId = taskDetails.categoryId || selectedTaskList;
        console.log(`Creating new task in list ${listId} with data:`, JSON.stringify(taskData, null, 2));
        await createTask(taskData, listId);
      }
      
      handleCloseDialog();
    } catch (err) {
      console.error('Failed to save task:', err);
    }
  };

  // 
  const handleCloseDialog = () => {
    setOpenDialog(false);
    
    // 
    setTaskDetails({
      taskId: null,
      title: '',
      description: '',
      dueDate: '',
      priority: 'normal',
      categoryId: ''
    });
  };

  // 
  const handleTaskDetailChange = (field) => (e) => {
    setTaskDetails({
      ...taskDetails,
      [field]: e.target.value
    });
  };
  
  // 
  const handleDeleteTask = async (taskId) => {
    // 
    setTaskToDelete(taskId);
    setDeleteTaskDialogOpen(true);
  };

  // 
  const confirmDeleteTask = async () => {
    try {
      if (taskToDelete) {
        // useTodo
        await deleteTask(taskToDelete);
        // ID
        setDeleteTaskDialogOpen(false);
        setTaskToDelete(null);
      }
    } catch (err) {
      console.error(':', err);
    }
  };

  // 
  const cancelDeleteTask = () => {
    setDeleteTaskDialogOpen(false);
    setTaskToDelete(null);
  };

  // 
  const getListColor = () => {
    // 
    return '#333333'; // 
  };
  
  // 
  const getThemeColor2 = (type) => {
    // 
    const colors = {
      primary: '#333333', // 
      secondary: '#555555', // 
      accent: '#777777', // 
      background: '#f9fafb', // 
      text: '#333333', // 
      border: '#e0e0e0' // 
    };
    
    return colors[type] || colors.primary;
  };

  // 
  const handleDragOver = (e) => {
    e.preventDefault();
  };

  // 
  const handleDrop = (e, toIndex) => {
    const fromIndex = e.dataTransfer.getData('text/plain');
    moveTask(parseInt(fromIndex), toIndex);
  };

  // 
  const handleDragStart = (e, index, taskId) => {
    e.dataTransfer.setData('text/plain', index);
    e.dataTransfer.setData('taskId', taskId);
    
    // 
    e.currentTarget.style.opacity = '0.6';
  };

  // 
  const handleDragEnd = (e) => {
    e.currentTarget.style.opacity = '1';
  };

  return (
    <Box sx={{ 
      width: '100%', 
      display: 'flex', 
      flexDirection: 'column'
    }}>
      <Box 
        className={isMobile ? 'header-mobile' : ''}
        sx={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          alignItems: 'center', 
          mb: 3, 
          pl: isMobile ? 0 : 2, 
          pt: isMobile ? 0 : 2,
          pr: isMobile ? 0 : 2
        }}
      >
        {isMobile ? (
          <>
            {/*  */}
            <Typography 
              variant="h5" 
              sx={{ fontWeight: 600 }}
              className="header-title-mobile"
              noWrap
              title={getListTitle()} // 
            >
              {getListTitle()}
            </Typography>
            
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {/*  */}
              <IconButton
                className="mobile-action-button"
                onClick={() => {
                  setTaskDetails({
                    ...taskDetails,
                    title: ''
                  });
                  setEditMode(false);
                  setOpenDialog(true);
                }}
                sx={{ 
                  color: 'primary.main',
                  mr: 1
                }}
              >
                <Box component="span" sx={{ fontSize: '1.5rem' }}>+</Box>
              </IconButton>
            </Box>
          </>
        ) : (
          <>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Typography variant="h5" 
                sx={{ fontWeight: 600 }}
                noWrap
                title={getListTitle()}
              >
                {/*  */}
                {getListTitle()}
              </Typography>
              
              {/*  -  */}
              {selectedTaskList && selectedTaskList !== 'all' && !filters.some(filter => filter.id === selectedTaskList) && !selectedFilter && (
                <IconButton
                  size="small"
                  onClick={handleOpenDeleteDialog}
                  sx={{ 
                    color: 'text.secondary',
                    ml: 0.5,
                    '&:hover': { bgcolor: 'rgba(0, 0, 0, 0.04)' }
                  }}
                  title=""
                >
                  <Box component="span" sx={{ fontSize: '1.2rem', display: 'block' }} className="emoji-icon"></Box>
                </IconButton>
              )}
            </Box>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              {/*  */}
              <Box 
                sx={{ 
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  p: 1.5,
                  border: '2px dashed #e0e0e0',
                  borderRadius: '8px',
                  cursor: 'pointer',
                  transition: 'all 0.2s ease',
                  mr: 2,
                  '&:hover': {
                    borderColor: getThemeColor('primary'),
                    bgcolor: 'rgba(0,0,0,0.02)'
                  }
                }}
                onClick={() => {
                  setTaskDetails({
                    ...taskDetails,
                    title: ''
                  });
                  setEditMode(false);
                  setOpenDialog(true);
                }}
              >
                <Box component="span" sx={{ fontSize: '1.2rem', mr: 1, color: 'var(--primary-color)' }} className="emoji-icon">+</Box>
                <Typography variant="body2" sx={{ color: getThemeColor('primary'), fontWeight: 500 }}>
                  
                </Typography>
              </Box>

              {/*  */}
              <Typography 
                variant="body2" 
                component="label" 
                className={isMobile ? 'completed-tasks-toggle-mobile' : ''}
                sx={{ 
                  display: 'flex', 
                  alignItems: 'center', 
                  cursor: 'pointer',
                  userSelect: 'none',
                  mr: 1
                }}
              >
                <Checkbox 
                  checked={showCompleted} 
                  onChange={toggleShowCompleted}
                  size="small"
                />
                
              </Typography>
              
              {/*  */}
              <IconButton
                size="small"
                onClick={handleOpenSettingsDialog}
                sx={{ 
                  color: 'text.secondary',
                  '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' },
                  mr: 0.5
                }}
                title=""
              >
                <Box component="span" sx={{ fontSize: '1.2rem', display: 'block' }} className="emoji-icon"></Box>
              </IconButton>
            </Box>
          </>
        )}
      </Box>
      
      {/*  */}
      <Dialog 
        open={settingsDialogOpen} 
        onClose={() => setSettingsDialogOpen(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Box sx={{ py: 2 }}>
            <Typography variant="h6" gutterBottom></Typography>
            
            <FormControl component="fieldset" sx={{ mt: 2, display: 'block' }}>
              <Typography variant="subtitle2" gutterBottom></Typography>
              <RadioGroup
                row
                value="light"
                onChange={() => {}}
              >
                <FormControlLabel value="light" control={<Radio />} label="" />
                <FormControlLabel value="dark" control={<Radio />} label="" />
                <FormControlLabel value="system" control={<Radio />} label="" />
              </RadioGroup>
            </FormControl>
            
            <FormControl fullWidth sx={{ mt: 3 }}>
              <InputLabel></InputLabel>
              <Select
                value="yyyy-MM-dd"
                onChange={() => {}}
                label=""
              >
                <MenuItem value="yyyy-MM-dd">YYYY-MM-DD</MenuItem>
                <MenuItem value="MM/dd/yyyy">MM/DD/YYYY</MenuItem>
                <MenuItem value="dd/MM/yyyy">DD/MM/YYYY</MenuItem>
                <MenuItem value="yyyyMMdd">YYYYMMDD</MenuItem>
              </Select>
            </FormControl>
            
            <Divider sx={{ my: 3 }} />
            
            <Typography variant="h6" gutterBottom></Typography>
            
            <FormControlLabel
              control={<Checkbox checked={true} onChange={() => {}} />}
              label=""
              sx={{ display: 'block', mt: 1 }}
            />
            
            <FormControl fullWidth sx={{ mt: 2 }}>
              <InputLabel></InputLabel>
              <Select
                value="1day"
                onChange={() => {}}
                label=""
              >
                <MenuItem value="same"></MenuItem>
                <MenuItem value="1day">1</MenuItem>
                <MenuItem value="3days">3</MenuItem>
                <MenuItem value="1week">1</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setSettingsDialogOpen(false)}></Button>
          <Button 
            onClick={() => setSettingsDialogOpen(false)} 
            variant="contained"
            sx={{ 
              bgcolor: getThemeColor('primary'),
              '&:hover': {
                bgcolor: getThemeColor('secondary')
              }
            }}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog 
        open={openDialog} 
        onClose={handleCloseDialog} 
        fullWidth 
        maxWidth="sm"
        fullScreen={isMobile}
        PaperProps={{
          sx: {
            maxHeight: isMobile ? '100%' : '90vh',
            width: isMobile ? '100%' : undefined,
            margin: isMobile ? 0 : undefined
          }
        }}
      >
        <DialogTitle>{editMode ? '' : ''}</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label=""
              fullWidth
              value={taskDetails.title}
              onChange={handleTaskDetailChange('title')}
              required
              autoFocus
            />
            
            <TextField
              label=""
              fullWidth
              multiline
              rows={4}
              value={taskDetails.description}
              onChange={handleTaskDetailChange('description')}
            />
            
            <TextField
              label=""
              type="date"
              fullWidth
              value={taskDetails.dueDate || ''}
              onChange={handleTaskDetailChange('dueDate')}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                style: { height: '24px', paddingTop: '8px', paddingBottom: '8px' },
                className: 'custom-date-input'
              }}
              sx={{
                '& .MuiOutlinedInput-root': {
                  '&:hover fieldset': {
                    borderColor: '#3498db',
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: '#2c3e50',
                  }
                },
                '& .MuiInputLabel-root.Mui-focused': {
                  color: '#2c3e50',
                }
              }}
            />
            
            <Box>
              <Typography variant="subtitle2" gutterBottom></Typography>
              <RadioGroup
                row
                value={taskDetails.priority}
                onChange={handleTaskDetailChange('priority')}
              >
                <FormControlLabel value="normal" control={<Radio sx={{ '&.Mui-checked': { color: getThemeColor('primary') } }} />} label="" />
                <FormControlLabel value="starred" control={<Radio sx={{ '&.Mui-checked': { color: getThemeColor('primary') } }} />} label="" />
              </RadioGroup>
            </Box>
            
            {taskLists && taskLists.length > 0 && (
              <FormControl fullWidth>
                <InputLabel></InputLabel>
                <Select
                  value={taskDetails.categoryId || selectedTaskList}
                  onChange={handleTaskDetailChange('categoryId')}
                  label=""
                >
                  {taskLists.map((list) => (
                    <MenuItem key={list.id} value={list.id}>
                      {list.title}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}
          </Box>
        </DialogContent>
        <DialogActions sx={{ justifyContent: 'flex-end' }}>
          <Button 
            onClick={handleCloseDialog}
            size="medium"
            sx={{ 
              minWidth: '80px',
              fontSize: '0.875rem'
            }}
          >
            
          </Button>
          <Button 
            onClick={handleSaveTask} 
            variant="contained" 
            color="primary"
            disabled={!taskDetails.title}
            size="medium"
            sx={{ 
              minWidth: '80px',
              fontSize: '0.875rem'
            }}
          >
            {editMode ? '' : ''}
          </Button>
        </DialogActions>
      </Dialog>
      
      {/*  */}
      {error && (
        <Box sx={{ mb: 3, mx: 2 }}>
          <Alert 
            severity="error" 
            action={
              <Button 
                color="inherit" 
                size="small"
                onClick={() => {
                  // 401
                  if (error.includes('401') || error.includes('') || error.includes('invalid authentication credentials')) {
                    // 
                    localStorage.removeItem('google_access_token');
                    localStorage.removeItem('google_auth_token');
                    localStorage.removeItem('google_user_info');
                    sessionStorage.removeItem('google_access_token');
                    sessionStorage.removeItem('google_auth_token');
                    sessionStorage.removeItem('google_user_info');
                    
                    // 
                    const authEvent = new CustomEvent('googleAuthStateChanged', { 
                      detail: { isAuthenticated: false } 
                    });
                    window.dispatchEvent(authEvent);
                    
                    // 
                    window.location.reload();
                  } else {
                    // 
                    window.location.reload();
                  }
                }}
              >
                {error.includes('401') || error.includes('') || error.includes('invalid authentication credentials') 
                  ? '' 
                  : ''
                }
              </Button>
            }
          >
            {error}
          </Alert>
        </Box>
      )}
      
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
          <CircularProgress />
        </Box>
      ) : todos.length === 0 ? (
        <Box sx={{ textAlign: 'center', my: 4, p: 3, border: '1px solid #e0e0e0', borderRadius: 1, mx: 2 }}>
          <Typography variant="body1" color="text.secondary">
            
          </Typography>
        </Box>
      ) : (
        <Box sx={{ 
          border: '1px solid #e0e0e0',
          borderRadius: '4px',
          mx: isMobile ? 1 : 2
        }}>
          <List sx={{ p: 0 }}>
            {todos.map((task, index) => (
              <React.Fragment key={task.id}>
                <ListItem 
                  draggable={!isMobile}
                  onDragStart={(e) => !isMobile && handleDragStart(e, index, task.id)}
                  onDragEnd={handleDragEnd}
                  onDragOver={handleDragOver}
                  onDrop={(e) => handleDrop(e, index)}
                  onClick={() => isMobile ? handleEditTask(task) : null}
                  onDoubleClick={() => !isMobile && handleEditTask(task)}
                  className={isMobile ? 'task-item-mobile' : ''}
                  sx={{ 
                    py: 1.5,
                    px: 2,
                    bgcolor: getTaskBackgroundColor(task),
                    borderLeft: isMobile ? 'none' : `4px solid ${getThemeColor('primary')}`,
                    '&:hover': { 
                      bgcolor: getThemeColor('background'),
                      cursor: isMobile ? 'pointer' : 'grab'
                    },
                    '&:active': {
                      cursor: isMobile ? 'pointer' : 'grabbing'
                    }
                  }}
                >
                  {isMobile ? (
                    // 
                    <>
                      <Box sx={{ display: 'flex', width: '100%', alignItems: 'flex-start' }}>
                        <Checkbox 
                          checked={task.status === 'completed'} 
                          onChange={(e) => {
                            e.stopPropagation(); // 
                            toggleTaskCompletion(task.id, task.status);
                          }}
                          sx={{ 
                            mr: 1,
                            width: 22,
                            height: 22,
                            borderRadius: '50%',
                            '&.Mui-checked': {
                              color: getThemeColor('primary'),
                            },
                            flexShrink: 0
                          }}
                        />
                        <Box sx={{ flex: 1, width: 'calc(100% - 30px)', maxWidth: 'calc(100% - 30px)' }}>
                          <Typography 
                            variant="body1" 
                            className="task-title-mobile"
                            sx={{
                              textDecoration: task.status === 'completed' ? 'line-through' : 'none',
                              color: task.status === 'completed' ? 'text.secondary' : 'text.primary',
                              mb: 0.5,
                              fontWeight: 500,
                              fontSize: '0.9375rem',
                              wordBreak: 'break-word !important',
                              whiteSpace: 'normal !important',
                              overflow: 'visible !important',
                              textOverflow: 'initial !important',
                              hyphens: 'auto !important',
                              maxWidth: '100% !important',
                              width: '100% !important',
                              display: 'block !important'
                            }}
                          >
                            {task.title}
                          </Typography>
                          <Box 
                            className="task-details-mobile"
                            sx={{ display: 'flex', flexWrap: 'wrap', alignItems: 'center', fontSize: '0.75rem', color: 'text.secondary' }}
                          >
                            <Box sx={{ display: 'flex', alignItems: 'center', mr: 2, mb: 0.5 }}>
                              <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                              {task.startDate ? format(parseISO(task.startDate), 'yyyyMMdd', { locale: ja }) : ''}
                            </Box>
                            {task.starred && (
                              <Box 
                                className="task-meta-item-mobile"
                                sx={{ display: 'flex', alignItems: 'center', mr: 2, mb: 0.5 }}
                              >
                                <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                                
                              </Box>
                            )}
                            {/*  */}
                            {task.listId && (
                              <Box 
                                className="task-meta-item-mobile"
                                sx={{ display: 'flex', alignItems: 'center', mb: 0.5 }}
                              >
                                <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                                {getTaskListName(task)}
                              </Box>
                            )}
                          </Box>
                        </Box>
                      </Box>
                    </>
                  ) : (
                    // 
                    <>
                      <Checkbox 
                        checked={task.status === 'completed'} 
                        onChange={(e) => {
                          e.stopPropagation(); // 
                          toggleTaskCompletion(task.id, task.status);
                        }}
                        sx={{ 
                          mr: 1,
                          width: 22,
                          height: 22,
                          borderRadius: '50%',
                          '&.Mui-checked': {
                            color: getThemeColor('primary'),
                          }
                        }}
                      />
                      <Box sx={{ flex: 1 }}>
                        <Typography 
                          variant="body1" 
                          sx={{
                            textDecoration: task.status === 'completed' ? 'line-through' : 'none',
                            color: task.status === 'completed' ? 'text.secondary' : 'text.primary',
                            mb: 0.5,
                            fontWeight: 500,
                            fontSize: '0.9375rem'
                          }}
                        >
                          {task.title}
                        </Typography>
                        <Box 
                          sx={{ display: 'flex', alignItems: 'center', fontSize: '0.75rem', color: 'text.secondary' }}
                        >
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                            {task.startDate ? format(parseISO(task.startDate), 'yyyyMMdd', { locale: ja }) : ''}
                          </Box>
                          {task.starred && (
                            <Box 
                              sx={{ display: 'flex', alignItems: 'center', ml: 2 }}
                            >
                              <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                              
                            </Box>
                          )}
                          {/*  */}
                          {task.listId && (
                            <Box 
                              sx={{ display: 'flex', alignItems: 'center', ml: 2 }}
                            >
                              <span className="emoji-icon" style={{ marginRight: '4px' }}></span>
                              {getTaskListName(task)}
                            </Box>
                          )}
                        </Box>
                      </Box>
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <IconButton 
                          size="small" 
                          sx={{ 
                            color: 'text.secondary',
                            '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' }
                          }}
                          onClick={() => handleEditTask(task)}
                        >
                          <Box 
                            component="span" 
                            sx={{ 
                              fontSize: '1rem',
                              display: 'inline-block'
                            }}
                            className="emoji-icon"
                          >
                            
                          </Box>
                        </IconButton>
                        <IconButton 
                          size="small" 
                          sx={{ 
                            color: 'text.secondary',
                            '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' }
                          }}
                          onClick={() => handleDeleteTask(task.id)}
                        >
                          <Box 
                            component="span" 
                            sx={{ 
                              fontSize: '1rem',
                              display: 'inline-block'
                            }}
                            className="emoji-icon"
                          >
                            
                          </Box>
                        </IconButton>
                      </Box>
                    </>
                  )}
                </ListItem>
                {index < todos.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        </Box>
      )}

      {/*  */}
      <Menu
        anchorEl={menuAnchorEl}
        open={Boolean(menuAnchorEl)}
        onClose={handleMenuClose}
        sx={{
          maxHeight: '300px', // 
        }}
      >
        {taskLists && taskLists.map((list) => (
          <MenuItem 
            key={list.id} 
            onClick={() => handleCategoryChange(list.id)}
            sx={{
              color: list.id === selectedTask?.listId ? 'primary.main' : 'inherit',
              fontWeight: list.id === selectedTask?.listId ? 500 : 400,
            }}
          >
            {list.title}
          </MenuItem>
        ))}
      </Menu>

      {/*  */}
      <Dialog
        open={deleteTaskDialogOpen}
        onClose={cancelDeleteTask}
        aria-labelledby="delete-task-dialog-title"
        aria-describedby="delete-task-dialog-description"
      >
        <DialogTitle id="delete-task-dialog-title">
          
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="delete-task-dialog-description">
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={cancelDeleteTask}></Button>
          <Button onClick={confirmDeleteTask} color="error" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*  */}
      <Dialog
        open={deleteDialogOpen}
        onClose={handleCloseDeleteDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {selectedListInfo.title}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDeleteDialog}></Button>
          <Button onClick={handleDeleteTaskList} color="error" autoFocus>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default TodoList;
