import React, { useState, useMemo } from 'react';
import { 
  Box, 
  Typography, 
  List, 
  ListItem, 
  ListItemIcon, 
  ListItemText, 
  InputBase, 
  Paper,
  TextField,
  IconButton,
  ClickAwayListener,
  Tooltip
} from '@mui/material';
import { useTodo } from '../contexts/TodoContext';
import { isToday, parseISO, startOfDay, isBefore, isTomorrow } from 'date-fns';

// 
const getThemeColor = (type) => {
  // 
  const colors = {
    primary: '#333333', // 
    secondary: '#555555', // 
    accent: '#777777', // 
    background: '#f9fafb', // 
    text: '#333333', // 
    border: '#e0e0e0' // 
  };
  
  return colors[type] || colors.primary;
};

const Sidebar = ({ closeSidebar, isMobile }) => {
  const { 
    taskLists, 
    selectedTaskList, 
    selectedFilter,
    selectTaskList, 
    selectFilter,
    updateTaskListTitle, 
    moveTaskToList,
    reorderTaskLists,
    createTaskList,
    searchTasks,
    allTodos, // 
    todos // 
  } = useTodo();
  const [editingListId, setEditingListId] = useState(null);
  const [editingTitle, setEditingTitle] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [isAddingNewList, setIsAddingNewList] = useState(false);
  const [newListTitle, setNewListTitle] = useState('');

  // 
  const filters = [
    { id: 'today', name: '', icon: '' },
    { id: 'after-tomorrow', name: '', icon: '' }, // 
    { id: 'past', name: '', icon: '' },
    { id: 'starred', name: '', icon: '' },
    { id: 'all', name: '', icon: '' },
  ];

  // 
  const filterCounts = useMemo(() => {
    // 
    const incompleteTodos = allTodos.filter(todo => todo.status !== 'completed');
    
    // 
    const counts = {};
    
    // 
    counts['all'] = incompleteTodos.length;
    
    // 
    counts['today'] = incompleteTodos.filter(todo => {
      if (!todo.startDate) return false;
      try {
        const date = parseISO(todo.startDate);
        return isToday(date);
      } catch (e) {
        return false;
      }
    }).length;
    
    // 
    counts['after-tomorrow'] = incompleteTodos.filter(todo => {
      if (!todo.startDate) return false;
      try {
        // 
        const date = parseISO(todo.startDate);
        const today = new Date();
        const startOfWeek = startOfDay(new Date(today));
        // 0: , 1: , ..., 6: 
        const dayOfWeek = today.getDay();
        // 
        startOfWeek.setDate(today.getDate() - dayOfWeek);
        
        // 
        const endOfWeek = new Date(startOfWeek);
        endOfWeek.setDate(startOfWeek.getDate() + 6);
        endOfWeek.setHours(23, 59, 59, 999); // 23:59:59.999
        
        // 
        return date >= startOfWeek && date <= endOfWeek;
      } catch (e) {
        return false;
      }
    }).length;
    
    // 
    counts['past'] = incompleteTodos.filter(todo => {
      if (!todo.startDate) return false;
      try {
        const date = parseISO(todo.startDate);
        return isBefore(date, startOfDay(new Date()));
      } catch (e) {
        return false;
      }
    }).length;
    
    // 
    counts['starred'] = incompleteTodos.filter(todo => todo.starred === true).length;
    
    return counts;
  }, [allTodos]); // allTodos

  // 
  const listCounts = useMemo(() => {
    const counts = {};
    
    // 
    const incompleteTodos = allTodos.filter(todo => todo.status !== 'completed');
    
    // 
    taskLists.forEach(list => {
      counts[list.id] = incompleteTodos.filter(todo => todo.listId === list.id).length;
    });
    
    return counts;
  }, [allTodos, taskLists]); // allTodostaskLists

  // 
  const handleStartEditing = (list) => {
    setEditingListId(list.id);
    setEditingTitle(list.title);
  };

  // 
  const handleSaveEditing = () => {
    if (editingListId && editingTitle.trim()) {
      updateTaskListTitle(editingListId, editingTitle.trim());
      setEditingListId(null);
      setEditingTitle('');
    }
  };

  // 
  const handleCancelEditing = () => {
    setEditingListId(null);
    setEditingTitle('');
  };

  // 
  const handleDragEnd = (e) => {
    // HTML5 Drag and Drop API
    const fromIndex = parseInt(e.dataTransfer.getData('listIndex'));
    const toIndex = parseInt(e.currentTarget.dataset.index);
    
    if (isNaN(fromIndex) || isNaN(toIndex) || fromIndex === toIndex) return;
    
    // 
    const newTaskLists = Array.from(taskLists);
    const [movedItem] = newTaskLists.splice(fromIndex, 1);
    newTaskLists.splice(toIndex, 0, movedItem);
    
    // 
    reorderTaskLists(newTaskLists);
  };

  // 
  const handleDragStart = (e, index) => {
    e.dataTransfer.setData('listIndex', index);
    e.currentTarget.style.opacity = '0.6';
  };

  // 
  const handleDragOver = (e) => {
    e.preventDefault();
    e.currentTarget.style.backgroundColor = '#f0f0f0';
  };

  // 
  const handleDragLeave = (e) => {
    e.currentTarget.style.backgroundColor = '';
  };

  // 
  const handleDragEndItem = (e) => {
    e.currentTarget.style.opacity = '1';
    e.currentTarget.style.backgroundColor = '';
  };

  // 
  const handleDrop = (e, listId) => {
    e.preventDefault();
    e.currentTarget.style.backgroundColor = '';
    
    const taskId = e.dataTransfer.getData('taskId');
    if (taskId) {
      moveTaskToList(taskId, listId);
    } else {
      handleDragEnd(e);
    }
  };

  // 
  const handleSearchChange = (e) => {
    setSearchQuery(e.target.value);
    // TodoContext
    searchTasks(e.target.value);
  };
  
  // 
  const handleAddNewList = () => {
    setIsAddingNewList(true);
    setNewListTitle('');
  };
  
  // 
  const handleSaveNewList = async () => {
    if (newListTitle.trim()) {
      try {
        await createTaskList(newListTitle.trim());
        setIsAddingNewList(false);
        setNewListTitle('');
      } catch (error) {
        console.error('Failed to create new list:', error);
      }
    } else {
      setIsAddingNewList(false);
    }
  };

  // 
  const handleCancelNewList = () => {
    setIsAddingNewList(false);
    setNewListTitle('');
  };
  
  // 
  const handleFilterClick = (filterId) => {
    selectFilter(filterId);
    if (isMobile) {
      closeSidebar();
    }
  };

  // 
  const handleTaskListClick = (listId) => {
    selectTaskList(listId);
    if (isMobile) {
      closeSidebar();
    }
  };

  return (
    <Box sx={{ 
      width: 320, 
      bgcolor: 'white', 
      borderRight: '1px solid #e0e0e0', // 
      display: 'flex',
      flexDirection: 'column',
      p: 2,
      height: '100vh', // 
      overflowY: 'auto',
      overflowX: 'hidden', // 
      maxWidth: '100%', // 
      minHeight: '100%' // 
    }}>
      {/*  */}
      <Box sx={{ 
        display: 'flex', 
        justifyContent: isMobile ? 'space-between' : 'flex-start',
        alignItems: 'center',
        mb: 3,
        pl: 1 // 
      }}>
        <Box 
          component="img" 
          src="/logo.png" 
          alt="SmartTodo Logo" 
          sx={{ 
            height: 36,
            width: 'auto',
            mr: 1.5
          }}
        />
        <Typography 
          variant="h4" 
          component="div" 
          sx={{ 
            fontWeight: 500, // 700500
            letterSpacing: '-0.5px',
            fontFamily: "'Poppins', 'Roboto', 'Helvetica', 'Arial', sans-serif",
            color: '#333333',
            textShadow: '0px 2px 4px rgba(0,0,0,0.1)',
            transform: 'scale(1.1)',
            transition: 'all 0.3s ease'
          }}
        >
          Smart<Box component="span" sx={{ fontWeight: 700, color: '#000000' }}>Todo</Box>
        </Typography>
        
        {/*  */}
        {isMobile && (
          <IconButton
            onClick={closeSidebar}
            sx={{
              color: 'text.secondary',
              '&:hover': { bgcolor: 'rgba(0,0,0,0.04)' }
            }}
          >
            <Box component="span" sx={{ fontSize: '1.5rem' }}></Box>
          </IconButton>
        )}
      </Box>
      
      {/*  -  */}
      {!isMobile && (
        <Box sx={{ position: 'relative', mb: 3 }}>
          <TextField
            fullWidth
            placeholder="..."
            variant="outlined"
            value={searchQuery}
            onChange={handleSearchChange}
            sx={{
              '& .MuiOutlinedInput-root': {
                pl: 4,
                bgcolor: '#f9fafb',
              }
            }}
          />
          <Box 
            sx={{ 
              position: 'absolute', 
              left: 12, 
              top: '50%', 
              transform: 'translateY(-50%)', 
              color: 'text.secondary', 
              fontSize: '1rem'
            }}
          >
            
          </Box>
        </Box>
      )}

      {/*  */}
      <Typography variant="caption" sx={{ 
        textTransform: 'uppercase', 
        letterSpacing: '0.05em', 
        color: 'text.secondary',
        mb: 1,
        pl: 1
      }}>
        
      </Typography>
      <List sx={{ mb: 2, p: 0 }}>
        {filters.map((filter) => (
          <ListItem
            key={filter.id}
            button
            selected={filter.id === selectedFilter}
            onClick={() => handleFilterClick(filter.id)}
            sx={{
              borderRadius: 1,
              mb: 0.5,
              '&.Mui-selected': {
                bgcolor: `${getThemeColor('background')}`,
                color: getThemeColor('primary'),
                fontWeight: 500,
              },
              '&:hover': {
                bgcolor: getThemeColor('background'),
              }
            }}
          >
            <ListItemIcon sx={{ minWidth: 36 }}>
              <Box component="span" sx={{ fontSize: '1.2rem' }}>{filter.icon}</Box>
            </ListItemIcon>
            <ListItemText 
              primary={`${filter.name}`}
              primaryTypographyProps={{ 
                fontSize: '0.9375rem',
                display: 'inline',
              }}
              secondary={`  (${filterCounts[filter.id] || 0})`}
              secondaryTypographyProps={{ 
                fontSize: '0.75rem', 
                color: 'text.disabled',
                display: 'inline',
                marginLeft: '4px',
              }}
              sx={{ display: 'flex', alignItems: 'center' }}
            />
          </ListItem>
        ))}
      </List>

      {/*  */}
      <Typography variant="caption" sx={{ 
        textTransform: 'uppercase', 
        letterSpacing: '0.05em', 
        color: 'text.secondary',
        mb: 1,
        pl: 1
      }}>
        
      </Typography>
      
      <List sx={{ p: 0 }}>
        {/*  */}
        {taskLists && taskLists.map((list, index) => (
          <ListItem
            key={list.id}
            button
            draggable
            data-index={index}
            onDragStart={(e) => handleDragStart(e, index)}
            onDragOver={handleDragOver}
            onDragLeave={handleDragLeave}
            onDragEnd={handleDragEndItem}
            onDrop={(e) => handleDrop(e, list.id)}
            selected={list.id === selectedTaskList}
            onClick={() => handleTaskListClick(list.id)}
            sx={{
              borderRadius: 1,
              mb: 0.5,
              py: 1,
              '&.Mui-selected': {
                bgcolor: `${getThemeColor('background')}`,
                color: getThemeColor('primary'),
                fontWeight: 500,
              },
              '&:hover': {
                bgcolor: getThemeColor('background'),
                cursor: 'grab'
              },
              '&:active': {
                cursor: 'grabbing'
              }
            }}
          >
            <ListItemIcon sx={{ minWidth: 36 }}>
              <Box component="span" sx={{ fontSize: '1.2rem' }}></Box>
            </ListItemIcon>
            {editingListId === list.id ? (
              <ClickAwayListener onClickAway={handleSaveEditing}>
                <TextField
                  value={editingTitle}
                  onChange={(e) => setEditingTitle(e.target.value)}
                  autoFocus
                  variant="standard"
                  fullWidth
                  onKeyDown={(e) => {
                    if (e.key === 'Enter') {
                      handleSaveEditing();
                    } else if (e.key === 'Escape') {
                      handleCancelEditing();
                    }
                  }}
                  sx={{ 
                    '& .MuiInput-root': { 
                      fontSize: '0.9375rem',
                      fontWeight: list.id === selectedTaskList ? 500 : 400
                    }
                  }}
                />
              </ClickAwayListener>
            ) : (
              <ListItemText 
                primary={list.title} 
                primaryTypographyProps={{ 
                  fontSize: '0.9375rem',
                  display: 'inline',
                  className: isMobile ? 'mylist-title-mobile' : ''
                }}
                secondary={`  (${listCounts[list.id] || 0})`}
                secondaryTypographyProps={{ 
                  fontSize: '0.75rem', 
                  color: 'text.disabled',
                  display: 'inline',
                  marginLeft: '4px',
                }}
                onDoubleClick={() => handleStartEditing(list)}
              />
            )}
          </ListItem>
        ))}
        
        {isAddingNewList ? (
          <ListItem sx={{ mt: 1, pl: 1 }}>
            <Box sx={{ display: 'flex', width: '100%', alignItems: 'center' }}>
              <TextField
                value={newListTitle}
                onChange={(e) => setNewListTitle(e.target.value)}
                autoFocus
                placeholder=""
                variant="standard"
                fullWidth
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    handleSaveNewList();
                  } else if (e.key === 'Escape') {
                    handleCancelNewList();
                  }
                }}
                sx={{ 
                  '& .MuiInput-root': { 
                    fontSize: '0.9375rem'
                  }
                }}
              />
              <IconButton 
                size="small" 
                onClick={handleSaveNewList}
                sx={{ ml: 1, color: 'primary.main' }}
              >
                <Box component="span" sx={{ fontSize: '0.875rem' }}></Box>
              </IconButton>
              <IconButton 
                size="small" 
                onClick={handleCancelNewList}
                sx={{ color: 'text.secondary' }}
              >
                <Box component="span" sx={{ fontSize: '0.875rem' }}></Box>
              </IconButton>
            </Box>
          </ListItem>
        ) : (
          <ListItem 
            button 
            onClick={handleAddNewList}
            sx={{ 
              mt: 1, 
              borderRadius: 1,
              color: 'text.secondary',
              '&:hover': { 
                bgcolor: getThemeColor('background'),
                color: 'primary.main'
              }
            }}
          >
            <ListItemIcon sx={{ minWidth: 36 }}>
              <Box component="span" sx={{ fontSize: '1.2rem' }}></Box>
            </ListItemIcon>
            <ListItemText 
              primary="" 
              primaryTypographyProps={{ 
                fontSize: '0.9375rem',
                fontWeight: 400
              }}
            />
          </ListItem>
        )}
      </List>
    </Box>
  );
};

export default Sidebar;
